	export default {
		data() {
			return {
				name: '',
				number: '',
				tel: ''
			}
		},
		onLoad() {
			
		},
		onShow() {
			this.getinfo();
		},
		methods: {
			getinfo(){
				uni.request({
					url: this.$api+'/weixin/shishenxin/indexuserinfo',
					data: this.getmd5({
						openId:uni.getStorageSync('openId'),
					}),
					header: {
						'content-type': 'application/json'
					},
					method: "POST",
					success: (res) => {
						if (res.data.code != 200) {
							uni.showToast({
							icon: 'none',
							title: res.data.msg,
							duration: 2000
							});
						} else {
							uni.hideLoading();
							var data = res.data.data;
							this.name = data.userName;
							this.tel = data.phone;
							this.number = data.userCode;
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
						icon: 'none',
						title: '获取失败',
						duration: 2000
						});
					}
				});
			},
			goTo() {
// 				uni.navigateTo({
// 					url: '../updatePWD/updatePWD'
// 				})
				uni.showToast({
					icon:'none',
					title:"当前功能完善中",
					duration:2000,
					mask:true
				})
			},
			tuichu() {
				uni.showModal({
					title: '退出',
					content: '退出将清除所有缓存',
					success: function(res) {
						if (res.confirm) {
							uni.clearStorage();
							uni.reLaunch({
								url:'../login/login'
							})
							
						} else if (res.cancel) {
						}
					}
				});
			}
		}
	}
